import router from "@/router";
import { computed } from "vue";

export const POLIST = [
  "publicOrder",
  "availablePublicOrderList",
  "publicOrderAuth",
  "publicOrderProfile",
  "/publicOrder",
  "/availablePublicOrderList",
  "/publicOrderAuth",
  "/publicOrderProfile",
];

export const isPublicOrder = computed(() =>
  POLIST.includes(router?.currentRoute?.value?.name)
);

export function useOrder() {
  return {};
}
